import React from 'react';
import logo from './logo.svg';
import './App.css';

async function onFileDrop(event) {
  event.preventDefault()
  await onFile(event.dataTransfer.items[0].getAsFile())
}
async function onFile(file) {
  const content = await readAsTextAsync(file)
  document.write(content)
}

async function readAsTextAsync(f) {
  return new Promise((resolve, reject) => {
    try {
      var reader = new FileReader();
      reader.onloadend = ()=>resolve(reader.result)
      reader.readAsText(f)
    }
    catch (e) {
      reject(e)
    }
  })
}

function App() {
  return (
    <div className="App">
      <header onDrop={(e)=>onFileDrop(e)} onDragOver={e=>e.preventDefault()} className="App-header">
        <label htmlFor="fi">
          <img src={logo} className="App-logo" alt="logo" />
        </label>
          <input style={{display:'none'}} id="fi" type='file' onChange={async (event) => {
            await onFile(event.target.files[0])}
            } />        <p>
            Edit <code>src/App.js</code> and save to reload.
        </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
